.section-1 .box-list {
  color: #fff;
}

.section-2 {
  margin-top: 10rem;
}

.section-2 > .title {
  color: #fff;
  margin-bottom: 1rem;
  font-size: 2.5rem;
  font-weight: 500;
}

.section-2 > .sub-title {
  color: #fff;
  margin-bottom: 3rem;
