  color: #fff;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.section-2 .form .btn-container input[type="checkbox"] {
  display: none;
}

.section-2 .form .btn-container .custom-checkbox {
  width: 18px;
  height: 18px;
  cursor: pointer;
  background-color: #29323e;
